function getDate(type) {
	const date = new Date();
	let year = date.getFullYear();
	let month = date.getMonth() + 1;
	let day = date.getDate();

	if (type === 'start') {
		year = year - 60;
	} else if (type === 'end') {
		year = year;
	}
	month = month > 9 ? month : '0' + month;;
	day = day > 9 ? day : '0' + day;

	return `${year}-${month}-${day}`;
};
export default {
	data() {
		return {
			height: '',
			time: '',
			data: '',
			date: getDate({
				format: true
			}),
			startDate: getDate('start'),
			endDate: getDate('end'),
			orders: '',
			merchantId: '',
			tg: true,
			opens: -1,
			height_a:"",
			display:"",
		}
	},
	onLoad() {
		let that = this;
		uni.getStorage({
			key: 'userinfo',
			success(e) {
				that.merchantId = e.data.id;
				that.orderDay();
			}
		});
	},
	onReady() {
		this.getTopheight();
	},
	methods: {
		getTopheight: function() {
			let that = this;
			uni.getSystemInfo({
				success(res) {
					that.height = res.statusBarHeight + 44 + "px";
					that.height_a = res.statusBarHeight + 44 +45+ "px";
				}
			})
		},
		get_time: function(e) {
			this.time = e.target.value
		},
		wz: function(latitude, longitude) {
			uni.openLocation({
				latitude: latitude,
				longitude: longitude,
				success: function(res) {
					console.log('res');
				}
			});
		},
		dh: function(phone) {
			uni.makePhoneCall({
				phoneNumber: phone,
				success: function(res) {
					console.log(res)
				}
			});
		},
		query: function() {
			let that = this;
			uni.request({
				url: this.serveipd + '/api/merchant/order/completedTime',
				method: 'GET',
				data: {
					merchantId: that.merchantId,
					createTime: that.time,
				},
				success: res => {
					console.log(res)
					that.orders = res.data.data.datas;
					if(that.orders==[]){
						that.display==true;
					}else{
						that.display==false;
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		open: function(index) {
			this.tg = !this.tg;
			if (this.opens != index) {
				this.opens = index;
			} else {
				this.opens = -1;
			}
		},
		orderDay: function() {
			let that = this;
			uni.request({
				url: this.serveipd + '/api/merchant/order/orderDay',
				method: 'GET',
				data: {
					merchantId: that.merchantId,
				},
				success: res => {
					console.log(res);
					that.orders = res.data.data.datas;
				},
				fail: () => {},
				complete: () => {}
			});
		},

	}
}
